body {
  background-color: #f3f3ff !important;
  margin: 0;
  font-family: Arial, sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* App.css */
.react-calendar__tile {
  display: flex;
  justify-content: center;
  align-items: center;
}

.react-calendar__tile abbr {
  visibility: hidden;
}

.custom-tile {
  font-size: 12px; 
}

input {
  width: 68%;
  padding: 8px 10px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 12px;
}

.add-task{
 margin-bottom: 15px; 
 font-size: 13px;
}

button {
  padding: 5px 10px;
  font-size: 12px;
  margin-left: 10px;
  margin-top: 4px;
  margin-bottom: 4px;
  background-color: #ffa5e8fc;
  color: white;
  border: none;
  border-radius: 5px;
}

button:hover {
  background-color: #ffabea8d;
}

ul {
  list-style-type: none;
  padding: 0;
  margin-top: 20px;
}
/*스크롤 설정*/
.modal-content ul {
  max-height: 200px; 
  overflow-y: auto;   
  padding-right: 10px;
}

li {
  background-color: #dfe2f4;
  width: 90%;
  padding: 5px 10px;
  margin-bottom: 5px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: start;
  position: relative; 
}

li span {
  flex-grow: 1;
  cursor: pointer;
  margin-left: 8px;
}

input[type="text"] {
  background-color: #dfe2f4; 
  color: rgb(0, 0, 0); 
  margin-left: 5px;
  border: none;
  outline: none;
  padding: 5px;
  flex: 1;
}


.modal-content ul li {
  font-size: 12px; 
}


input[type="checkbox"] {
  width: 5px;
  height: 5px;
  border-radius: 15%; 
  border: 2px solid #ffffff; 
  background-color: white;
  appearance: none;
  transition: background-color 0.3s, border-color 0.3s; 
}

input[type="checkbox"]:checked {
  background-color: #3f436c; 
  border-color: #3f436c;
  position: relative;
}

input[type="checkbox"]:checked::after {
  content: '✔'; 
  position: absolute;
  top: 0;
  left: 4px;
  color: white;
  font-size: 12px;
}


.delete-btn {
  position: absolute; 
  top: 50%; 
  right: 10px; 
  transform: translateY(-50%); 
  color: rgba(110, 109, 112, 0.419);
  font-size: 13px;
  cursor: pointer;
  transition: opacity 0.3s ease;
  opacity: 1;
}


.todo-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  padding: 10px;
  border-bottom: 1px solid #ddd;
  position: relative;
  transition: transform 0.3s ease;
}
.todo-item:hover {
  transform: translateX(-50px); 
}


/* 달력 전체 */
.react-calendar {
  width: 350px;
  max-width: 100%;
  background: #fff9fbd9;
  border: none;
  border-radius: 15px;
  padding: 10px;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.2);
}


.react-calendar__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(3, 11, 72, 0.784);
  border-radius: 30px;
  padding: 5px;
  color: white;
  font-weight: bold;
}


.react-calendar__month-view__weekdays {
  text-transform: uppercase;
  font-size: 14px;
  color: #3c3c3c;
  font-weight: bold;
}

.react-calendar__tile {
  color: rgb(66, 66, 66);
  text-align: center;
  padding: 10px;
  border-radius: 50%;
  transition: 0.3s;
}

.react-calendar__tile--active {
  background: #000a67 !important;
  color: white !important;
  font-weight: bold;
  border-radius: 50%; 
}


.react-calendar__tile.has-todo {
  position: relative;
}


.react-calendar__tile.has-todo::after {
  content: "";
  position: absolute;
  bottom: 5px; 
  left: 50%;
  transform: translateX(-50%); 
  width: 6px;
  height: 6px;
  border-radius: 50%; 
  background-color: #000a67; 
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*모달 속 버튼*/
.modal-content button:first-of-type,
.modal-content button:last-of-type {
  background-color: white;
  color: black;
  border: 1px solid #ccc;
}

.modal-content button:first-of-type:hover,
.modal-content button:last-of-type:hover {
  background-color: #f0f0f0;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); 
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; 
}

.modal-content {
  background: rgba(255, 255, 255, 0.932);
  padding: 15px;
  border-radius: 15px;
  width: 300px;
  text-align: center;
}

.modal-content h4 {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 15px;
}


.modal-content h4 button {
  margin: 0 10px; 
}

